.root {
  position: relative; }

.input {
  word-break: break-all;
  resize: none; }
  .input > span {
    word-break: break-all;
    resize: none; }
  .input.hiddenCaret {
    color: transparent;
    text-shadow: 0 0 0 var(--main-text-color); }
    .input.hiddenCaret > * {
      color: transparent;
      text-shadow: 0 0 0 var(--main-text-color); }

.hidden {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  visibility: hidden;
  user-select: none;
  pointer-events: none;
  word-break: break-all;
  resize: none; }
